import http from './http'
import Qs from 'qs'
// 封装 get 请求 返回的是一个promise
export function get (url, params) {
  return new Promise((resolve, reject) => {
    http.get(url, { params }).then(res => {
      resolve(res.data)
    }).catch(err => {
      reject(err)
    })
  })
}
// 封装 post 请求 返回的是一个promise
export function post (url, params) {
  console.log(JSON.stringify(params))
  return new Promise((resolve, reject) => {
    http.post(url, JSON.stringify(params), {
      headers: {
        'Content-Type': 'application/json;charset=UTF-8'
      }
    }).then(res => {
      resolve(res.data)
    }).catch(err => {
      reject(err)
    })
  })
}
// 获取验证码
export const getCodes = data => post('api/code', data)
// 机构登录
export const getLogin = data => post('api/ysj/adminlogin', data)
// 用户登录
export const getUserLogin = data => post('api/ysj/login', data)
// 注册
export const getUserRegister = data => post('api/ysj/register', data)
// 新闻
export const getUserNotify = data => post('api/ysj/getNotify', data)
// 服务机构
export const getRole = data => post('api/ysj/role', data)
export const getOffice = data => post('api/ysj/findByOffice', data)
export const getParent = data => post('api/ysj/findByParentId', data)
export const getRoleId = data => post('api/ysj/roleById', data)



