    },
    getIndex() {
      if(this.activeIndex == 1) {
        this.$router.push({path: '/index'})
      }
    },
    handleSelect(key, keyPath) {
      this.reg = true
      if (key == 1) {
        this.$router.push({path: '/index'})
      } else if (key == 2) {
        this.$router.push({path: '/mechanism'})
      } else if (key == 3) {
        this.$router.push({path: '/service'})
      } else if (key == 8) {
        this.$router.push({path: '/laws'})
      } else if (key == 9) {
        this.$router.push({path: '/classicCase'})
      } else if (key == 6) {
        this.$router.push({path: '/NewsInformation'})
      } else if (key == 7) {
        this.$router.push({path: '/helpCenter'})
      } else if (key == 10) {
        this.$router.push({path: '/boutique'})
      }
    },
    personal() {
      this.$router.push({path: '/personal'})
    },
    getShopCode(value){
      if(value) {
        this.reg = false
      } else {
        this.reg = true
      }
      console.log(value);
    },
    getOut() {
      const data = {}
      this.$store.commit('SET_MENU', data)
      this.$router.push({path: '/index'})
    }
  },
}
