import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld,
      children: [
        {
          path: '/index',
          name: 'index',
          component: () => import('@/views/shouYe'),
          meta: {
            title: '首页'
          }
        },
        {
          path: '/mechanism',
          name: 'mechanism',
          component: () => import('@/views/mechanism'),
          meta: {
            title: '服务机构'
          }
        },
        {
          path: '/service',
          name: 'service',
          component: () => import('@/views/service'),
          meta: {
            title: '服务内容'
          }
        },
        {
          path: '/laws',
          name: 'laws',
          component: () => import('@/views/laws'),
          meta: {
            title: '法律法规'
          }
        },
        {
          path: '/classicCase',
          name: 'classicCase',
          component: () => import('@/views/classicCase'),
          meta: {
            title: '经典案例'
          }
        },
        {
          path: '/NewsInformation',
          name: 'NewsInformation',
          component: () => import('@/views/NewsInformation'),
          meta: {
            title: '新闻动态'
          }
        },
        {
          path: '/helpCenter',
          name: 'helpCenter',
          component: () => import('@/views/helpCenter'),
          meta: {
            title: '帮助中心'
          }
        },
        {
          path: '/userRegister',
          name: 'userRegister',
          component: () => import('@/components/userRegister'),
          meta: {
            title: '注册'
          }
        },
        {
          path: '/personal',
          name: 'personal',
          component: () => import('@/views/personal'),
          meta: {
            title: '个人中心'
          }
        },
        {
          path: '/boutique',
          name: 'boutique',
          component: () => import('@/views/boutique'),
          meta: {
            title: '精品推荐'
          }
        },
      ]
    }
  ]
})



